/* REGULAR */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), local("Roboto-Regular"), url("./Roboto-Regular.ttf") format('ttf');
}

/* BOLD */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("./Roboto-Bold.ttf") format('ttf');
}
