/* REGULAR */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), local("Roboto-Regular"), url(/static/media/Roboto-Regular.18d44f79.ttf) format('ttf');
}

/* BOLD */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url(/static/media/Roboto-Bold.7c181887.ttf) format('ttf');
}

/* IMPORT - STYLES OF SLIDER COMPONENT */

/* IMPORT - STYLES OF COLLAPSE COMPONENT */

/* IMPORT - FONTS */

* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}

body {
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.section1 {
  height: 100vh;
  text-align: center;
}

/* SLIDER */
.section1 .slick-slider,
.section1 .slick-list,
.section1 .slick-track,
.section1 .slick-slide > div {
  height: 100vh !important;
}

/* SLIDER - DOTS */
.section1 .slick-dots {
  background-color: rgba(50, 50, 50, 0.8);
  bottom: 0;
  height: 72px;
  margin-top: -72px;
  overflow: hidden;
  padding: 20px 0;
  position: relative;
}

.section1 .slick-dots li,
.section1 .slick-dots li button {
  height: 30px;
  width: 30px;
}

.section1 .slick-dots li button:before {
  background-color: #ffffff;
  border-radius: 50%;
  border: 5px solid #313231;
  content: "";
  opacity: 1;
}

.section1 .slick-dots li.slick-active button:before {
  background-color: #4d6128;
  border-color: #ffffff;
}

/* SLIDER - IMAGES */
.section1 .bg-img {
  height: 100%;
  padding: 20px;
  position: relative;
}

.section1 .img1 {
  background-image: url(/static/media/img1.6b4c9ce7.jpg);
  background-position: 40% 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

/* SLIDER - TEXT */
.section1 h1 {
  color: #4d6128;
  font-size: 32px;
  font-weight: bold;
  margin: 5px 0px;
  text-transform: uppercase;
}

@media screen and (min-width: 769px) {
  .section1 h1 {
    font-size: 60px;
  }
}

.section1 b {
  color: #213800;
  font-size: 13px;
  font-weight: 700;
}

@media screen and (min-width: 769px) {
  .section1 b {
    font-size: 24px;
    font-weight: 700;
  }
}

/* ARROW */
.section1 .arrowDown,
.section1 .arrowDown img {
  display: inline-block;
}

.section1 .arrowDown {
  margin-top: 20px;
}

.section1 .arrowDown img {
  width: 20px;
}

@media screen and (min-width: 769px) {
    .section1 .arrowDown img {
        width: 30px;
    }
}
.section2 {
  background-color: #454545;
  color: #ffffff;
  padding: 20px 0;
  text-align: center;
}

/* SLIDER */
.section2 .slick-slider {
  max-width: calc(100% - 120px);
  margin: 0 auto;
}

@media screen and (max-width: 400px) {
  .section2 .slick-slider {
    max-width: calc(100% - 100px);
  }
}

@media screen and (min-width: 1201px) {
  .section2 .slick-slider {
    max-width: 1024px;
  }
}

/* SLIDER - ARROWS */
.section2 .slick-arrow {
  height: 40px;
  width: 40px;
}

@media screen and (max-width: 400px) {
  .section2 .slick-arrow {
    height: 30px;
    width: 30px;
  }
}

.section2 .slick-arrow::before {
  background-image: url(data:image/png;base64,);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  content: "";
  display: block;
  -webkit-filter: invert(100%);
          filter: invert(100%);
  height: 40px;
  margin: 0 auto !important;
  position: relative;
  width: 30px;
}

@media screen and (max-width: 400px) {
  .section2 .slick-arrow::before {
    height: 30px;
    width: 20px;
  }
}

.section2 .slick-prev {
  left: -45px;
}

@media screen and (max-width: 400px) {
  .section2 .slick-prev {
    left: -35px;
  }
}

.section2 .slick-prev::before {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}

.section2 .slick-next {
  right: -45px;
}

@media screen and (max-width: 400px) {
  .section2 .slick-next {
    right: -35px;
  }
}

.section2 .slick-next::before {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

/* SLIDER - ITEM */
.section2 .slider-item {
  padding: 20px;
}

/* SLIDER - IMAGE */
.section2 .slider-item img {
  border: 8px solid #6f6f6f;
  margin: 0 auto;
  width: 100%;
}

/* SLIDER - TEXT */
.section2 .slider-item .title {
  display: block;
  font-size: 20px;
  margin: 10px 0;
  text-transform: uppercase;
}

.section2 .slider-item p {
  font-size: 14px;
  font-weight: 300;
  margin: 0;
}

.section3 {
  background-color: #6f6f6f;
  color: #ffffff;
  padding: 40px;
  text-align: left;
}

@media screen and (max-width: 768px) {
  .section3 {
    padding: 40px 20px;
  }
}

/* CONTAINER */
.section3 .container {
  margin: 0 auto;
  max-width: 1210px;
}

.section3:not(.enable-backward-compatibility) .container {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

@media screen and (min-width: 769px) {
  .section3:not(.enable-backward-compatibility) .container {
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
  }
}

.section3.enable-backward-compatibility .container {
  display: table;
}

.section3.enable-backward-compatibility .container > * {
  display: table-cell;
}

/* IMAGE */
.section3 .img {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 50%;
  border: 15px solid #343434;
}

@media screen and (max-width: 400px) {
  .section3 .img {
    height: 250px;
    width: 250px;
  }
}

@media screen and (min-width: 401px) and (max-width: 768px) {
  .section3 .img {
    height: 300px;
    width: 300px;
  }
}

@media screen and (min-width: 769px) and (max-width: 992px) {
  .section3 .img {
    height: 270px;
    width: 270px;
  }
}

@media screen and (min-width: 993px) {
  .section3 .img {
    height: 300px;
    width: 300px;
  }
}

/* TEXT */
.section3 .title {
  font-size: 28px;
  text-transform: uppercase;
}

.section3 .text {
  font-size: 13px;
  padding: 0 20px;
  width: 100%;
}

@media screen and (max-width: 768px) {
  .section3 .text {
    margin-top: 40px;
  }
}

@media screen and (min-width: 769px) {
  .section3 .text {
    width: 60%;
  }
}

.section3 .text p:last-child {
  margin-bottom: 0px;
}

.section4 {
  background-color: #bebebe;
  padding: 40px;
}

@media screen and (max-width: 768px) {
  .section4 {
    padding: 20px;
  }
}

/* COLLAPSIBLE */
.section4 .rc-collapse {
  border-radius: 5px;
  border: none;
  margin: 0 auto;
  max-width: 1140px;
  overflow: hidden;
}

/* COLLAPSIBLE - ITEM */
.section4 .rc-collapse-item {
  -webkit-transition: all linear 0.2s;
  transition: all linear 0.2s;
}

.section4 .rc-collapse-item:not(.rc-collapse-item-active) {
  background-color: #6f6f6f;
  color: #ffffff;
}

/* COLLAPSIBLE - HEADER */
.section4 .rc-collapse-header {
  font-weight: 900;
  position: relative;
}

@media screen and (min-width: 769px) {
  .section4 .rc-collapse-header {
    font-size: 18px;
    padding: 20px !important;
  }
}

.section4 .rc-collapse-item:not(.rc-collapse-item-active) .rc-collapse-header {
  color: #ffffff;
}

.section4 .rc-collapse-item-active .rc-collapse-header {
  background-color: #ffffff;
}

/* COLLAPSIBLE - ARROW */
.section4 .rc-collapse-header img.arrow {
  border: none !important;
  height: 17px !important;
  margin: 0 !important;
  position: absolute !important;
  right: 15px !important;
  top: 11px !important;
  -webkit-transition: -webkit-transform linear 200ms;
  transition: -webkit-transform linear 200ms;
  transition: transform linear 200ms;
  transition: transform linear 200ms, -webkit-transform linear 200ms;
  width: auto !important;
}

@media screen and (min-width: 769px) {
  .section4 .rc-collapse-header img.arrow {
    right: 21px !important;
    top: 21px !important;
  }
}

.section4 .rc-collapse-header img.arrow.expanded {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.section4
  .rc-collapse-item:not(.rc-collapse-item-active)
  .rc-collapse-header
  img.arrow {
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

/* COLLAPSIBLE - CONTENT */
.section4:not(.enable-backward-compatibility) .rc-collapse-content .content {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

@media screen and (min-width: 769px) {
  .section4:not(.enable-backward-compatibility) .rc-collapse-content .content {
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
  }
}

.section4.enable-backward-compatibility .rc-collapse-content .content {
  display: table;
}

.section4.enable-backward-compatibility .rc-collapse-content .content > * {
  display: table-cell;
}

/* COLLAPSIBLE - CONTENT IMAGE */
.section4 .rc-collapse-content .content .img {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  border: 10px solid #6f6f6f;
  height: 200px;
  width: 300px;
}

@media screen and (max-width: 768px) {
  .section4 .rc-collapse-content .content .img {
    margin: 0 auto;
    max-width: 300px;
    width: 100%;
  }
}

@media screen and (min-width: 769px) {
  .section4 .rc-collapse-content .content .img {
    min-width: 300px;
  }
}

/* COLLAPSIBLE - CONTENT TEXT */
.section4 .rc-collapse-content .content p {
  font-size: 13px;
}

@media screen and (max-width: 768px) {
  .section4 .rc-collapse-content .content p {
    width: 100%;
  }
}

@media screen and (min-width: 769px) {
  .section4 .rc-collapse-content .content p {
    padding: 0 20px;
  }
}

.section5.gradient {
  background: #6f6f6f;
  background: -webkit-gradient(
    left top,
    right bottom,
    color-stop(0%, #6f6f6f),
    color-stop(50%, #6f6f6f),
    color-stop(50%, #454545),
    color-stop(100%, #454545)
  );
  background: -webkit-linear-gradient(
    to bottom right,
    #6f6f6f 0%,
    #6f6f6f 50%,
    #454545 50%,
    #454545 100%
  );
  background: -webkit-linear-gradient(
    top left,
    #6f6f6f 0%,
    #6f6f6f 50%,
    #454545 50%,
    #454545 100%
  );
  background: linear-gradient(
    to bottom right,
    #6f6f6f 0%,
    #6f6f6f 50%,
    #454545 50%,
    #454545 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#6f6f6f', endColorstr='#454545', GradientType=1 );

  padding: 20px;
}

@media screen and (min-width: 769px) {
  .section5.gradient {
    padding: 40px;
  }
}

/* FORM */
.section5 form {
  margin: 0 auto;
  max-width: 1140px;
}

/* FORM - COLS */
@media screen and (max-width: 768px) {
  .section5 form .col-right .field {
    margin-bottom: 0px;
  }
}

@media screen and (min-width: 769px) {
  .section5 form .col {
    display: inline-block;
    width: 50%;
  }

  .section5 form .col-left {
    padding-right: 6px;
  }

  .section5 form .col-right {
    padding-left: 6px;
  }

  .section5 .col .field {
    margin: 0;
  }
}

/* FORM - FIELD */
.section5 .field {
  background-color: #bebebe;
  border-radius: 10px;
  display: inline-block;
  margin: 12px 0;
  padding: 13px 20px;
  width: 100%;
}

.section5 .field:first-child {
  margin-top: 0;
}

.section5 .field input,
.section5 .field textarea {
  background-color: rgba(0,0,0,0);
  border: none;
  box-shadow: none;
  font-size: 14px;
  height: 44px;
  margin-bottom: -15px;
  margin-top: -15px;
}

/* FORM - FIELD LABEL */
.section5 .field label {
  float: left;
  font-size: 14px;
  font-weight: 900;
  line-height: 22px;
  padding-right: 10px;
  min-width: 65px;
}

/* FORM - FIELD INPU */
.section5 .field input {
  width: calc(100% - 65px);
}

/* FORM - FIELD PHONE LABEL */
.section5 .field-phone label {
  width: 80px;
}

/* FORM - FIELD PHONE INPUT */
.section5 .field-phone input {
  width: calc(100% - 80px);
}

/* FORM - FIELD MESSAGE LABEL */
.section5 .field-message label {
  width: 90px;
}

/* FORM - FIELD MESSAGE INPUT */
.section5 .field-message textarea {
  height: 140px;
  margin: 0;
  width: calc(100% - 90px);
}

/* FORM - ACTION BUTOTNS */
.section5 .actions {
  padding-top: 10px;
  text-align: center;
}

.section5 form button {
  background-color: #ffffff;
  border-radius: 7px;
  border: none;
  cursor: pointer;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  margin: 0 auto;
  padding: 15px 35px;
  text-transform: uppercase;
}

/* FORM - DIALOG */
.section5 #dialog {
  background-color: rgba(0, 0, 0, 0.4);
  display: none;
  height: 100%;
  left: 0;
  opacity: 0;
  padding: 10px;
  position: fixed;
  top: 0;
  -webkit-transition: all ease-in-out 150ms;
  transition: all ease-in-out 150ms;
  width: 100%;
}

.section5 #dialog.active {
  display: block;
}

.section5 #dialog.active.animate {
  opacity: 1;
}

.section5 #dialog div {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 2px 5px #333333;
  height: 215px;
  margin: auto;
  margin-top: 32vh;
  padding: 20px;
  text-align: center;
  -webkit-transition: all ease-in-out 150ms;
  transition: all ease-in-out 150ms;
  -webkit-transform: scale(0.8);
          transform: scale(0.8); 
  width: 300px;
}

.section5 #dialog.active.animate div {
  -webkit-transform: scale(1);
          transform: scale(1); 
}

.section5 #dialog h1 {
  font-size: 30px;
}

.section5 #dialog button {
  background-color: transparent;
  border-radius: 5px;
  border: 1px solid #aaaaaa;
  cursor: pointer;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  margin-top: 25px;
  padding: 10px 35px;
  text-transform: uppercase;
  -webkit-transition: all linear 150ms;
  transition: all linear 150ms;
}

.section5 #dialog button:hover {
  background-color: #eeeeee;
  border-color: #dddddd;
}

.footer {
    background-color: #343434;
    color: #ffffff;
    font-size: 17px;
    font-weight: 900;
    margin-top: -1px;
    padding: 20px;
    text-align: center;
    text-transform: uppercase;
}
